import './App.css';
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faInstagram, faYoutube, faVimeoV } from '@fortawesome/free-brands-svg-icons'

function App() {
  return (
    <div className="App">

      <div className="brand fixed-top">
        <div className="row">
          <div className="col-5 py-2 px-5 d-none d-lg-block">
            
            <div className="row">¡Muy pronto! descubre lo que trae la nueva web, </div>
            <div className="row">nos estamos preparando para la XXIX muestra Macroregional norte de teatro</div>
          </div>
          <div className="col-1 d-none d-lg-block">
          </div>
          <div className="col-lg-6 col-md-12 pt-4 pb-5">
            <div className="row social-media">
              <div className="col-3 d-none d-lg-block"></div>
              <div className="col-lg-2 col-3"><a href="https://www.facebook.com/GALATEARTESCENICAS"><FontAwesomeIcon icon={faFacebookF} /></a></div>
              <div className="col-lg-2 col-3"><a href="https://www.instagram.com/galateartescenicas/"><FontAwesomeIcon icon={faInstagram} /></a></div>
              <div className="col-lg-2 col-3"><a href="https://www.youtube.com/channel/UCD6jS3sLjMQvxUK4I1xP6Nw/videos"><FontAwesomeIcon icon={faYoutube} /></a></div>
              <div className="col-lg-2 col-3"><a href="https://vimeo.com/user104148126"><FontAwesomeIcon icon={faVimeoV} /></a></div>
              <div className="col-1 d-none d-lg-block"></div>
            </div>
          </div>
        </div>
      </div>

      <Navbar className="gala-nav" fixed="top">
        <div className="container">
          <Nav className="">
            <Nav.Link className="" href="#home">INICIO</Nav.Link>
          </Nav>
          <Nav className="">
            <Nav.Link className="" href="#features">PARTICIPANTES</Nav.Link>
          </Nav>
          <Nav className="">
            <Nav.Link className="" href="#pricing">PROGRAMA</Nav.Link>
          </Nav>
          <Nav className="d-none d-lg-block">
            <img src="https://kfervera.github.io/GalateaFiles/Logos/Logo_(4).png" alt="Galatea Logo"></img>
          </Nav>
          <Nav className="">
            <Nav.Link className="" href="#home">BLOG</Nav.Link>
          </Nav>
          <Nav className="">
            <Nav.Link className="" href="#features">CONTACTANOS</Nav.Link>
          </Nav>
          <Nav className="">
            <Nav.Link className="" href="#pricing">FAQ</Nav.Link>
          </Nav>
        </div>
      </Navbar>

      <header className="App-header">
        <h1>Ésta pronto será la web de galatea</h1>
        <h2>Página en construcción</h2>
      </header>
    </div>
  );
}

export default App;
